.content-loader{
    background-color: var(--blanco);
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 9999;
    transition: all 1.5s;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader,
.loader:before,
.loader:after{
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
    animation: loader 1.5s infinite ease-in-out;
}
.loader{
    color: var(--blue);
    font-size: 1.3rem;
    position: relative;
    animation-delay: -0.16s ;
}
.loader:before,
.loader:after{
    content: '';
    position: absolute;
    top: 0;
}
.loader:before{
    left: -3.5rem;
    animation-delay: -0.32s;
}
.loader:after{
    left: 3.5rem;
}

@keyframes loader {
0%,
80%,
100%{ 
    box-shadow: 0 2.5em 0 -1.3em;
}
40%{
    box-shadow: 0 2.5em 0 0;
}
}